import React from 'react';
import ReactDOM from 'react-dom';
import ReactCSSTransitionGroup  from 'react-addons-css-transition-group';
import './index.css';
import 'materialize-css/dist/css/materialize.min.css';

import Input from './templ/Input';
import Card from './templ/Card';

class App extends React.Component {
    state = {
        tasks: [],
        first: [],
        second: [],
        third: []
    }

    submit = (text, portal) => {
        const mass = this.state.tasks;
        mass.push({id: ++ mass.length, text: text, portal: portal});
        this.setState({
            first: mass.filter((todo, item) => todo.portal === 'first'),
            second: mass.filter((todo, item) => todo.portal === 'second'),
            third: mass.filter((todo, item) => todo.portal === 'third')
        })

    }

    remove = (id) => {
        const {tasks, first, second, third} = this.state;
        this.setState({
            tasks: tasks.filter((todo, item) => todo.id !== id),
            first: first.filter((todo, item) => todo.id !== id),
            second: second.filter((todo, item) => todo.id !== id),
            third: third.filter((todo, item) => todo.id !== id)
        })
    }

    saveNewText = (text, id) => {
        let arr = this.state.tasks;
        let filt = arr.filter((todo, item) => todo.id === id);
        filt[0].text = text;
        this.setState({
            tasks: arr
        })
    }
    check = (item) => {
        this.setState({
            category: item
        })
    }
    portals = (nmb) => {
        return (
                <ReactCSSTransitionGroup transitionName="example" transitionEnter={true} transitionLeave={true} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
                {nmb.map((todo, id) => {
                    return <Card todo={todo} id={todo.id} key={todo.id} remove={this.remove} saveNewText={this.saveNewText} />
                })}</ReactCSSTransitionGroup>
            )
        }
    render() {
        const {first, second, third} = this.state;
        return (
            <div className="wrapper">

                    <Input submit={this.submit} />
                    {ReactDOM.createPortal(this.portals(first), document.getElementById('first'))}
                    {ReactDOM.createPortal(this.portals(second), document.getElementById('second'))}
                    {ReactDOM.createPortal(this.portals(third), document.getElementById('third'))}

            </div>
        )
    }
};

ReactDOM.render(<App />, document.getElementById('root'));



